import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux';
import { compose } from "redux";
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import AddIcon from '@mui/icons-material/Add';
import Citrus from '../common/Citrus'
import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';
import { styled } from '@mui/material/styles';
import s2Icon from '../assets/s2.png'
import doiIcon from '../assets/doi.svg'
import pdfIcon from '../assets/pdf.png'
import bibTexIcon from '../assets/bibtex.png'
import { generateBibTexEntry, generateBibTexFilename } from '../../utils/bibtex'
import FileSaver from 'file-saver'

const HtmlTooltip = styled(({ className, ...props }) => (
    <Tooltip {...props} classes={{ popper: className }} />
))(({ theme }) => ({
    [`& .${tooltipClasses.tooltip}`]: {
        backgroundColor: 'transparent',
    },
}));

function saveFile(fileName, text) {
    const blob = new Blob([text], { type: "text/plain;charset=utf-8" });
    FileSaver.saveAs(blob, fileName);
}

function PublicationDetail({
    search,
    selectedPublication,
    onBrowseReferences,
    onBrowseCitations,
    addSeedPaper,
    addSeedPaperDisabled
}) {
    const [showAllAuthors, setShowAllAuthors] = useState(false);

    useEffect(() => {
        setShowAllAuthors(false)
    }, [selectedPublication]);

    if (!selectedPublication) {
        return null;
    }

    const publication = search.result.publicationById[selectedPublication]

    const year = (publication.year === 0 ? '' : publication.year)
    const journal = (publication.journalName ? publication.journalName : '')
    let yearAndJournal = '';
    if (year && journal) {
        yearAndJournal = `${journal} (${year})`
    } else if (journal) {
        yearAndJournal = journal
    } else if (year) {
        yearAndJournal = year
    }

    let authors
    if (!publication.authors?.length) {
        authors = ''
    } else if (publication.authors.length <= 2 || showAllAuthors) {
        authors = publication.authors.map((author) => author.name).reduce((prev, next) => (prev + ', ' + next))
    } else {
        const firstAuthors = publication.authors.slice(0, 2).map((author) => author.name).reduce((prev, next) => (prev + ', ' + next))
        authors =
            <div>
                {firstAuthors}
                <Button
                    variant="text"
                    style={{ textTransform: 'none', fontSize: 16, marginTop: -4, marginLeft: -8 }}
                    onClick={() => setShowAllAuthors(true)}
                    sx={{ p: 0, m: 0 }}
                >
                    et al.
                </Button>
            </div>
    }

    const pdfUrl = publication.pdfUrls.length > 0 ? publication.pdfUrls[0] : ''

    const addSeedDisabled = addSeedPaperDisabled(publication)

    return (
        <div style={{ padding: 16, overflow: 'scroll' }}>
            <div style={{ marginBottom: 20, fontSize: 18 }}>{publication.title}</div>
            <div style={{ marginBottom: 4, color: 'grey' }}>{yearAndJournal}</div>
            <div style={{ marginBottom: 14, color: 'grey' }}>{authors}</div>
            <div style={{ marginBottom: 20, display: 'flex' }}>
                <Tooltip title='Add to seed publications' placement='top'>
                    <IconButton
                        style={{ paddingTop: 4, marginRight: 8, display: 'inline-block', verticalAlign: 'top', cursor: 'pointer', flexShrink: '0' }}
                        onClick={() => addSeedPaper(publication)}
                        disabled={addSeedDisabled}
                        color='primary'
                        sx={{ p: 0 }}
                    >
                        <Citrus color1={addSeedDisabled ? 'grey' : '#1976d2'} color2={addSeedDisabled ? 'grey' : '#1976d2'} width={28} />
                        <AddIcon style={{ fontSize: 16, color: addSeedDisabled ? 'grey' : '#1976d2' }} />
                    </IconButton>
                </Tooltip>
                <Button
                    variant="outlined"
                    style={{ textTransform: 'none', fontFamily: 'Poppins', marginRight: 8, padding: '0 4px 0 4px' }}
                    onClick={() => onBrowseReferences()}
                    disabled={!publication.outCitations.length}
                >
                    {`References (${publication.outCitations.length})`}
                </Button>
                <Button
                    variant="outlined"
                    style={{ textTransform: 'none', fontFamily: 'Poppins', padding: '0 4px 0 4px' }}
                    onClick={() => onBrowseCitations()}
                    disabled={!publication.inCitations.length}
                >
                    {`Citations (${publication.inCitations.length})`}
                </Button>
                <div style={{ marginLeft: 12 }}>
                    {pdfUrl && (
                        <img
                            src={pdfIcon}
                            alt="pdf icon"
                            style={{ width: 28, marginRight: 8, cursor: 'pointer' }}
                            onClick={() => window.open(pdfUrl, '_newtab')}
                        />
                    )}
                    {publication.doiUrl && (
                        <img
                            src={doiIcon}
                            alt="doi icon"
                            style={{ width: 28, marginRight: 4, cursor: 'pointer' }}
                            onClick={() => window.open(publication.doiUrl, '_newtab')}
                        />
                    )}
                    {publication.s2Url && (
                        <img
                            src={s2Icon}
                            alt="semantic scholar icon"
                            style={{ width: 44, height: 32, marginRight: 4, cursor: 'pointer' }}
                            onClick={() => window.open(publication.s2Url, '_newtab')}
                        />
                    )}
                    <HtmlTooltip title={
                        <div style={{ width: 200, backgroundColor: '#fff', boxShadow: '0px 0px 5px grey', padding: 4 }}>
                            <div style={{ fontSize: 14, margin: '8px 0', fontFamily: 'Poppins', textAlign: 'center', color: 'black' }}>
                                BibTex entry
                        </div>
                            <Button
                                variant="outlined"
                                style={{ textTransform: 'none', fontFamily: 'Poppins', margin: '0 auto', marginBottom: 4 }}
                                onClick={() => navigator.clipboard.writeText(generateBibTexEntry(publication))}
                                sx={{ p: 0, display: 'block', width: '100%' }}
                            >
                                Copy to clipboard
                        </Button>
                            <Button
                                variant="outlined"
                                style={{ textTransform: 'none', fontFamily: 'Poppins', margin: '0 auto', marginBottom: 4 }}
                                onClick={() => saveFile(generateBibTexFilename(publication), generateBibTexEntry(publication))}
                                sx={{ p: 0, m: 0, display: 'block', width: '100%' }}
                            >
                                Download .bib
                        </Button>
                        </div>
                    }
                    >
                        <img
                            src={bibTexIcon}
                            alt="bibtex icon"
                            style={{ width: 20, cursor: 'pointer' }}
                            onClick={() => { }}
                        />
                    </HtmlTooltip>
                </div>
            </div>
            {publication.paperAbstract && (
                <div>
                    <div style={{ marginBottom: 10, fontWeight: 'bold' }}>Abstract</div>
                    <div style={{ marginBottom: 14, textAlign: 'justify' }}>{publication.paperAbstract}</div>
                </div>
            )}
        </div>
    );
}

function mapStateToProps(state) {
    return { search: state.search }
}

function mapDispatchToProps(dispatch) {
    return {
        dispatch,
    };
}

const withConnect = connect(mapStateToProps, mapDispatchToProps);
export default compose(withConnect)(PublicationDetail);