import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux';
import { compose } from "redux";
import LoadingIndicator from '../common/LoadingIndicator';
import { useLocation } from 'react-router-dom'
import Button from '@mui/material/Button'
import * as actions from '../../state-management/search/actions'
import SearchResults from './SearchResults';
import { MAX_SEED_PAPER_COUNT } from '../constants/constants'
import SearchContainer from './SearchContainer';



function Search({ search, dispatch }) {
    const location = useLocation()
    const [searchQuery, setSearchQuery] = useState('')
    const [seedPapers, setSeedPapers] = useState({})
    const [maxPublicationCount, setMaxPublicationCount] = useState(100)
    const [searchConfigExpanded, setSearchConfigExpanded] = useState(false)

    useEffect(() => {
        const params = new URLSearchParams(location.search);

        let limit = parseInt(params.get('l'))
        if (!isNaN(limit)) {
            setMaxPublicationCount(limit)
        } else {
            limit = null
        }

        const queryString = params.get('q')
        if (queryString) {
            setSearchQuery(queryString)
        }

        const seedsString = params.get('s')
        let seedPaperIds = []
        if (seedsString) {
            seedPaperIds = seedsString.split(',')
        }

        if (queryString || seedPaperIds.length) {
            setSearchConfigExpanded(false)
            dispatch(actions.search({
                textQuery: queryString ?? '',
                seedPaperIds: seedPaperIds,
                maxPublicationCount: limit ?? maxPublicationCount
            }))
        }
    }, [location]);

    useEffect(() => {
        if (!search.loading && !search.error && search.result) {
            const papers = search.query.seedPaperIds
                .filter((id) => search.result.publicationById.hasOwnProperty(id))
                .reduce((papers, id) => {
                    papers[id] = search.result.publicationById[id]
                    return papers
                }, {})
            setSeedPapers(papers)
        }
    }, [search])

    const seedPaperIds = Object.keys(seedPapers)

    const shouldHighlightSearchButton = () => {
        if (!search.loading && !search.error && search.result) {
            if (search.query.maxPublicationCount !== maxPublicationCount) return true
            if (search.query.seedPaperIds.join(',') !== seedPaperIds.join(',')) return true
        }
        return false
    }

    const addSeedPaper = (paper) => {
        if (!seedPapers.hasOwnProperty(paper.Id)) {
            setSeedPapers({
                [paper.Id]: paper,
                ...seedPapers
            })
            setSearchConfigExpanded(true)
        }
    }

    const addSeedPaperDisabled = (p) => {
        return Object.keys(seedPapers).length >= MAX_SEED_PAPER_COUNT || seedPapers.hasOwnProperty(p.Id)
    }

    return (
        <>
            <SearchContainer
                searchQuery={searchQuery}
                setSearchQuery={setSearchQuery}
                shouldHighlightSearchButton={shouldHighlightSearchButton}
                searchConfigExpanded={searchConfigExpanded}
                setSearchConfigExpanded={setSearchConfigExpanded}
                seedPapers={seedPapers}
                setSeedPapers={setSeedPapers}
                maxPublicationCount={maxPublicationCount}
                setMaxPublicationCount={setMaxPublicationCount}
                addSeedPaper={addSeedPaper}
                addSeedPaperDisabled={addSeedPaperDisabled}
            />
            <div style={{
                position: 'absolute',
                maxWidth: 500,
                top: 4,
                right: 24,
                zIndex: 2
            }}>
                <Button variant="text" sx={{ width: 80, height: 36, fontFamily: 'Poppins', marginTop: 1 }}>About</Button>
                <Button variant="text" sx={{ width: 80, height: 36, fontFamily: 'Poppins', marginTop: 1 }}>Donate</Button>
            </div>
            {
                search.loading && (
                    <div>
                        <LoadingIndicator size={100} style={{ marginTop: 260 }} />
                        <div style={{ textAlign: "center", marginTop: 12, color: 'grey', fontSize: 18 }}>
                            Searching for relevant publications...
                    </div>
                    </div>
                )
            }
            {
                search.error && !search.loading && (
                    <div>
                        <div style={{ textAlign: "center", marginTop: 260, fontSize: 42 }}>⚠️</div>
                        <div style={{ textAlign: "center", color: 'grey' }}>
                            {'Something went wrong. Trying again may help.'}
                        </div>
                    </div>
                )
            }
            {
                !search.loading && !search.error && search.result && (
                    <SearchResults
                        addSeedPaper={addSeedPaper}
                        addSeedPaperDisabled={addSeedPaperDisabled}
                    />
                )
            }
        </>
    );
}

function mapStateToProps(state) {
    return { search: state.search }
}

function mapDispatchToProps(dispatch) {
    return {
        dispatch,
    };
}

const withConnect = connect(mapStateToProps, mapDispatchToProps);
export default compose(withConnect)(Search);