import React, { useState } from 'react';
import IconButton from '@mui/material/IconButton';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import InfoIcon from '@mui/icons-material/Info';
import { Link } from 'react-router-dom';
import Citrus from '../common/Citrus';

function Legend() {
    const [showingLegend, shouldShowLegend] = useState(false)

    return (
        <div
            onMouseEnter={() => shouldShowLegend(true)}
            onMouseLeave={() => shouldShowLegend(false)}
            style={{
                position: 'absolute',
                bottom: 36,
                left: 36
            }}>
            {showingLegend && (
                <Card style={{ width: 340 }} sx={{ boxShadow: 3 }}>
                    <CardContent>
                        <div id="help-container">
                            <div class="legend-row">
                                <div id="regular-node" class="node-symbol"></div>
                                <div>publication</div>
                            </div>
                            <div class="legend-row">
                                <div style={{
                                    display: 'grid',
                                    gridTemplateColumns: '50% 50%',
                                    gridTemplateRows: '100%',
                                    columnGap: '0%',
                                    marginLeft: -6
                                }}>
                                    <div id="regular-node-small" class="node-symbol"></div>
                                    <div id="regular-node-medium" class="node-symbol"></div>
                                </div>
                                <div>the size of a publication's node represents its {<Link to='/about'>relevance score</Link>}.</div>
                            </div>
                            <div class="legend-row">
                                <div id="selected-node" class="node-symbol"></div>
                                <div>currently selected publication</div>
                            </div>
                            <div class="legend-row">
                                <Citrus color1={'rgb(100%,72.068787%,0%)'} color2={'rgb(49%,40%,58%)'} width={32} />
                                <div>a {<Link to='/about'>seed publication</Link>}</div>
                            </div>
                            <div class="legend-row">
                                <div id="citation-or-reference" class="link-symbol"></div>
                                <div>citation or reference</div>
                            </div>
                            <div class="legend-row" style={{ marginBottom: 0 }}>
                                <div id="similarity" class="link-symbol"></div>
                                <div>connection between {<Link to='/about'>similar</Link>} publications</div>
                            </div>
                        </div>
                    </CardContent>
                </Card>
            )}
            <div>
                <IconButton color="primary" aria-label="help">
                    <InfoIcon />
                </IconButton>
            </div>
        </div>
    );
}

export default Legend;