import React, { Component } from 'react';
import '../css/graph.css'
import * as d3 from 'd3v4';
import generateColorTuple from '../constants/colors';

const alwaysEnabledConsole = console.log

const citrusPaths = [
    { color: 0, d: 'M 701.5 1401.5 C 315.515625 1401.5 1.5 1087.480469 1.5 701.5 C 1.5 315.519531 315.515625 1.5 701.5 1.5 C 1087.480469 1.5 1401.5 315.519531 1401.5 701.5 C 1401.5 1087.480469 1087.480469 1401.5 701.5 1401.5 Z M 701.5 45.25 C 339.644531 45.25 45.25 339.640625 45.25 701.5 C 45.25 1063.359375 339.644531 1357.75 701.5 1357.75 C 1063.359375 1357.75 1357.75 1063.359375 1357.75 701.5 C 1357.75 339.640625 1063.359375 45.25 701.5 45.25' },
    { color: 1, d: "M 1281.378906 678.109375 C 1272.261719 681.609375 779.890625 678.695313 779.890625 678.695313 C 779.890625 678.695313 1129.769531 346.019531 1169.929688 351.261719 C 1210.101563 356.5 1256 428.660156 1283.371094 494.328125 C 1310.761719 559.996094 1303.28125 641.429688 1297.808594 658.902344 C 1292.339844 676.367188 1281.378906 678.109375 1281.378906 678.109375 " },
    { color: 1, d: "M 736.140625 646.578125 C 736.140625 646.578125 725.789063 113.96875 766.953125 109.96875 C 809.292969 93.601563 918.199219 124.550781 1000.390625 167.550781 C 1078.691406 208.539063 1129.671875 246.058594 1122.359375 289.738281 C 1115.050781 333.449219 736.140625 646.578125 736.140625 646.578125 " },
    { color: 0, d: "M 624.851563 673.835938 C 624.851563 673.835938 288.417969 359.160156 239.324219 352.589844 C 195 344.230469 149.390625 427.015625 123.777344 498.898438 C 96.65625 574.84375 84.128906 645.679688 133.5625 667.128906 C 192.285156 690.519531 624.851563 673.835938 624.851563 673.835938 " },
    { color: 0, d: "M 677.082031 646.578125 C 677.082031 646.578125 694.792969 143.601563 672.320313 113.269531 C 630.308594 56.460938 258.722656 193.550781 294.941406 289.71875 C 323.269531 364.871094 677.082031 646.578125 677.082031 646.578125 " },
    { color: 0, d: "M 121.621094 724.914063 C 130.742188 721.410156 624.851563 731.023438 624.851563 731.023438 C 624.851563 731.023438 273.21875 1057.011719 233.066406 1051.761719 C 192.894531 1046.519531 147 974.339844 119.621094 908.691406 C 92.234375 843.003906 99.703125 761.601563 105.191406 744.097656 C 110.660156 726.644531 121.621094 724.914063 121.621094 724.914063 " },
    { color: 0, d: "M 668.6875 756.421875 C 668.6875 756.421875 677.210938 1289.011719 636.046875 1293.03125 C 593.707031 1309.390625 484.777344 1278.449219 402.609375 1235.429688 C 324.304688 1194.460938 273.335938 1156.941406 280.621094 1113.238281 C 287.945313 1069.550781 668.6875 756.421875 668.6875 756.421875 " },
    { color: 0, d: "M 779.890625 725.488281 C 779.890625 725.488281 1114.578125 1043.808594 1163.671875 1050.390625 C 1208 1058.769531 1253.609375 975.980469 1279.21875 904.078125 C 1306.328125 828.15625 1318.871094 757.320313 1269.421875 735.871094 C 1210.730469 712.492188 779.890625 725.488281 779.890625 725.488281 " },
    { color: 0, d: "M 734.976563 756.421875 C 734.976563 756.421875 708.207031 1259.378906 730.691406 1289.710938 C 772.699219 1346.511719 1144.28125 1209.429688 1108.070313 1113.28125 C 1079.738281 1038.128906 734.976563 756.421875 734.976563 756.421875 " }
]

const SEED_PAPER_NODE_WIDTH = 90

export default class ForceGraph extends Component {

    constructor(props) {
        super(props);
        this.currentGraphHash = null
        this.currentGraph = null
        this.links = []
        this.nodes = []
        this.timeScale = null
        this.yAxisG = null
        this.zoom = null
        this.simulation = null
        this.selectedNode = null
        this.clippingToTimeline = true
        this.gMain = null
        this.gDraw = null
        this.width = 0
        this.height = 0
        this.resizeCallback = () => this.resize(this)
        this.resizeTimeout = null
        this.seedPaperIds = []
        this.seedPaperNodes = []
    }

    componentDidMount() {
        // init
        this.width = this.svg.node().getBoundingClientRect().width * this.props.graphWidth;
        this.height = this.svg.node().getBoundingClientRect().height;

        // add defs
        this.svg.selectAll('defs').remove();
        this.svg.append('defs').append('marker')
            .attr('id', 'arrowhead')
            .attr('viewBox', '-0 -5 10 10')
            .attr('refX', 10)
            .attr('refY', 0)
            .attr('orient', 'auto')
            .attr('markerWidth', 4)
            .attr('markerHeight', 4)
            .attr('xoverflow', 'visible')
            .append('svg:path')
            .attr('d', 'M 0,-5 L 10 ,0 L 0,5')
            .attr('fill', '#001182')
            .style('stroke', 'none');

        // remove any previous graphs
        this.svg.selectAll('.g-main').remove();

        this.gMain = this.svg.append('g')
            .classed('g-main', true)

        // add background
        this.gMain.append('rect')
            .classed('graph-background', true)
            .attr('width', '100%')
            .attr('height', '100%')

        // add graph
        this.gDraw = this.gMain.append('g')

        // add Y axis
        this.timeScale = d3.scaleLinear()
            .domain([this.props.graph.maxYear, this.props.graph.maxYear - 30])
            .range([this.width - 96, 96]);
        const yAxis = createTimeAxis(this.timeScale, this.width)
        this.yAxisG = this.gMain.append("g")
            .classed('y-axis', true)
            .call(yAxis)
            .attr("transform", "translate(" + (0) + "," + (this.height - 60) + ")");

        // Add zoom
        this.zoom = d3.zoom().on('zoom', () => zoomed(this.gDraw, this.timeScale, this.yAxisG));
        function zoomed(gDraw, timeScale, yAxisG) {
            gDraw.attr('transform', d3.event.transform);
            const newTimeScale = d3.event.transform.rescaleX(timeScale);
            yAxisG.call(createTimeAxis(newTimeScale))
        }
        this.gMain.call(this.zoom)

        window.addEventListener('resize', this.resizeCallback)

        this.shouldComponentUpdate(this.props)
    }


    shouldComponentUpdate({ graph, clipNodesToTimeline, selectedPublication, onPublicationSelected, graphWidth, seedPaperIds }) {
        this.onPublicationSelected = onPublicationSelected
        this.graphWidth = graphWidth

        const width = this.svg.node().getBoundingClientRect().width * graphWidth;
        const height = this.svg.node().getBoundingClientRect().height;

        // TODO: more efficient update logic?
        const graphHash = JSON.stringify(graph)
        if (graphHash === this.currentGraphHash
            && this.clippingToTimeline === clipNodesToTimeline
            && this.selectedPublicationId === selectedPublication
            && this.width === width
            && this.height === height) {
            return false;
        } else if (graphHash === this.currentGraphHash) {
            if (this.width !== width || this.height !== height) {
                this.resize()
            }

            if (this.clippingToTimeline !== clipNodesToTimeline) {
                this.clipNodesToTimeline(clipNodesToTimeline)
            }

            if (selectedPublication !== this.selectedPublicationId) {
                this.selectedPublicationId = selectedPublication
                let selectedNode;
                this.nodes.each((n) => {
                    if (n.Id === selectedPublication) {
                        selectedNode = n
                    }
                })
                setSelectedNode(selectedNode, this.nodes, this.links)
            }

            return false
        }

        this.currentGraphHash = graphHash
        this.currentGraph = JSON.parse(graphHash)
        this.clippingToTimeline = clipNodesToTimeline
        this.selectedPublicationId = selectedPublication
        this.seedPaperIds = seedPaperIds

        return this.redrawGraph()
    }

    redrawGraph() {
        this.initXCoordinates(this.currentGraph, this.clippingToTimeline)
        this.clipNodesToTimeline(this.clippingToTimeline)

        // remove possibly existing graph, stop possibly existing simulation
        this.simulation?.stop()
        this.gDraw.selectAll("g").remove()

        const allLinks = this.currentGraph.citations.concat(this.currentGraph.similarities)

        // draw graph
        // add links
        this.links = this.gDraw.append("g")
            .attr("class", "link")
            .selectAll("line")
            .data(allLinks)
            .enter().append("line")
            .attr('marker-end', (l) => {
                if (l.type !== 'similar'
                    && (this.selectedPublicationId === l.source || this.selectedPublicationId === l.target)) {
                    return 'url(#arrowhead)'
                }
                return 'none'
            })

        // add nodes
        this.nodes = this.gDraw.append("g")
            .attr("class", "node")
            .selectAll("circle")
            .data(this.currentGraph.publications)
            .enter().append("circle")
            .attr("r", (d) => {
                if (this.seedPaperIds.includes(d.Id)) {
                    return SEED_PAPER_NODE_WIDTH / 2
                }
                return d.relevanceScore * 30;
            })
            .attr("fill", (d) => {
                if (this.seedPaperIds.includes(d.Id)) {
                    return '#ffffff'
                }
                return 'rgb(100%,72.068787%,0%)';
            })
            .call(d3.drag()
                .on("start", (d) => {
                    this.onPublicationSelected(d.Id)
                    dragstarted(d, this.links, this.nodes, this.simulation)
                })
                .on("drag", dragged)
                .on("end", (d) => dragended(d, this.simulation, this.clippingToTimeline)));

        // add seed paper svgs
        this.seedPaperNodes = this.gDraw.append("g")
            .attr("class", "seed-paper")
            .selectAll("svg")
            .data(this.currentGraph.publications.filter((p) => this.seedPaperIds.includes(p.Id)))
            .enter().append("svg")
            .attr("viewBox", "0 0 1402 1402")
            .attr("width", SEED_PAPER_NODE_WIDTH)
            .attr("height", SEED_PAPER_NODE_WIDTH)
            .call(d3.drag()
                .on("start", (d) => {
                    this.onPublicationSelected(d.Id)
                    dragstarted(d, this.links, this.nodes, this.simulation)
                })
                .on("drag", dragged)
                .on("end", (d) => dragended(d, this.simulation, this.clippingToTimeline)));

        appendCitrusPaths(this.seedPaperNodes)

        function appendCitrusPaths(parent) {
            const group = parent.append('g')
            group.append("circle")
                .attr("r", '50%')
                .attr("cx", '701')
                .attr("cy", '701')
                .attr("fill", 'white')
            for (const path of citrusPaths) {
                group.append('path')
                    .attr('fill', function (d) { return generateColorTuple(d.Id)[path.color] })
                    .attr('stroke', 'none')
                    .attr('fill-rule', 'evenodd')
                    .attr('fill-opacity', '1')
                    .attr('d', path.d)
            }
        }

        // add titles for mouseover blurbs
        this.nodes.append("title").text(function (d) { return d.title });

        // create simulation
        this.simulation = d3.forceSimulation()
            .force("link", d3.forceLink()
                .id(function (d) { return d.Id; })
                .distance(function (d) { return 100; }))
            .force("charge", d3.forceManyBody().strength(-600))
            .force("x", d3.forceX(this.width / 2).strength(0.05))
            .force("y", d3.forceY(this.height / 2).strength(0.05))
            .force("collision", d3.forceCollide(20).iterations(30))

        this.simulation
            .nodes(this.currentGraph.publications)
            .on("tick", () => ticked(this.links, this.nodes, this.seedPaperNodes, this.selectedPublicationId));

        this.simulation.force("link").links(allLinks);

        // highlight selected node if present
        if (this.selectedPublicationId) {
            let selectedNode;
            this.nodes.each((n) => {
                if (n.Id === this.selectedPublicationId) {
                    selectedNode = n
                }
            })
            setSelectedNode(selectedNode, this.nodes, this.links)
        }

        function ticked(links, nodes, seedPaperNodes, selectedId) {
            // update node and line positions at every step of 
            // the force simulation
            links.attr("x1", function (d) { return d.source.x; })
                .attr("y1", function (d) { return d.source.y; })
                .attr("x2", function (d) {
                    if (d.target.Id === selectedId || d.source.Id === selectedId) {
                        const dy = d.target.y - d.source.y
                        const dx = d.target.x - d.source.x
                        const distance = Math.sqrt(Math.pow(dy, 2) + Math.pow(dx, 2))
                        const radius = d.target.relevanceScore * 30
                        const s = (distance - radius) / Math.max(distance, 0.001)

                        return d.source.x + s * dx
                    }
                    return d.target.x
                })
                .attr("y2", function (d) {
                    if (d.target.Id === selectedId || d.source.Id === selectedId) {
                        const dy = d.target.y - d.source.y
                        const dx = d.target.x - d.source.x
                        const distance = Math.sqrt(Math.pow(dy, 2) + Math.pow(dx, 2))
                        const radius = d.target.relevanceScore * 30
                        const s = (distance - radius) / Math.max(distance, 0.001)

                        return d.source.y + s * dy
                    }
                    return d.target.y
                });

            nodes.attr("cx", function (d) { return d.x; })
                .attr("cy", function (d) { return d.y; });

            seedPaperNodes.attr("x", function (d) { return d.x - SEED_PAPER_NODE_WIDTH / 2; })
                .attr("y", function (d) { return d.y - SEED_PAPER_NODE_WIDTH / 2; })
        }


        function dragstarted(d, links, nodes, simulation) {
            if (!d3.event.active) simulation.alphaTarget(0.1).restart();

            setSelectedNode(d, nodes, links);

            d.fx = d.x;
            d.fy = d.y;
        }

        function dragged(d) {
            d.fx += d3.event.dx;
            d.fy += d3.event.dy;
        }

        function dragended(d, simulation, clippingToTimeline) {
            if (!d3.event.active) simulation.alphaTarget(0);

            d.fy = null;

            if (clippingToTimeline) {
                d.fx = d.savedFx;
            } else {
                d.fx = null;
            }
        }

        return false
    }

    componentWillUnmount() {
        this.simulation?.stop()
        window.removeEventListener('resive', this.resizeCallback)
        this.svg.selectAll('.g-main').remove();
    }

    render() {
        return (
            <svg
                id="force-graph"
                xmlns="http://www.w3.org/2000/svg"
                ref={n => {
                    this.svg = d3.select(n);
                }}
                style={{
                    width: '100%',
                    height: '100%',
                    top: 0,
                    left: 0,
                    zIndex: -1000
                }}
            />
        );
    }

    resize() {
        if (this.resizeTimeout) {
            clearTimeout(this.resizeTimeout)
        }
        this.resizeTimeout = setTimeout(() => this.doResize(), 300)
    }

    doResize() {
        if (!this.svg.node()) return // this happens sometimes (?!)

        this.width = this.svg.node().getBoundingClientRect().width * this.graphWidth;
        this.height = this.svg.node().getBoundingClientRect().height;

        this.yAxisG.remove()

        // add Y axis
        this.timeScale = d3.scaleLinear()
            .domain([this.props.graph.maxYear, this.props.graph.maxYear - 30])
            .range([this.width - 96, 96]);
        const yAxis = createTimeAxis(this.timeScale)
        this.yAxisG = this.gMain.append("g")
            .classed('y-axis', true)
            .call(yAxis)
            .attr("transform", "translate(" + (0) + "," + (this.height - 60) + ")");

        this.initXCoordinates(this.currentGraph, this.clippingToTimeline)
        this.clipNodesToTimeline(this.clippingToTimeline)
    }


    clipNodesToTimeline(shouldClip) {
        if (shouldClip) {
            this.clippingToTimeline = true;
            const initXTransform = this.width * 0.55;
            const initYTransform = this.height / 3;
            this.gMain.call(this.zoom.transform, d3.zoomIdentity.translate(initXTransform, initYTransform).scale(0.35));
            this.yAxisG.attr("opacity", 1)
            this.simulation?.stop();
            if (this.nodes?.each) {
                this.nodes.each(function (d) {
                    d.fx = d.savedFx;
                })
            }
            this.simulation?.alpha(1).restart();
        } else {
            this.clippingToTimeline = false;
            const initXTransform = this.width / 3;
            const initYTransform = this.height / 3;
            this.gMain.call(this.zoom.transform, d3.zoomIdentity.translate(initXTransform, initYTransform).scale(0.35));
            this.yAxisG.attr("opacity", 0)
            this.simulation?.stop();
            if (this.nodes?.each) {
                this.nodes.each(function (d) {
                    d.fx = null;
                })
            }
            this.simulation?.alpha(1).restart();
        }
    }

    initXCoordinates(graph, clipNodesToTimeline) {
        for (var i = 0; i < graph.publications.length; i++) {
            if (clipNodesToTimeline) {
                // Some publications have year = 0. Don't fix y for those
                if (graph.publications[i].year) {
                    graph.publications[i].savedFx = this.timeScale(graph.publications[i].year)
                    graph.publications[i].fx = this.timeScale(graph.publications[i].year)
                }
            } else {
                graph.publications[i].savedFx = this.timeScale(graph.publications[i].year)
                graph.publications[i].fx = null
            }
        }
    }
}

function setSelectedNode(node, allNodes, allLinks) {
    allLinks.classed("citation", false);
    allLinks.classed("reference", false);
    allLinks.classed("similar", false);
    if (node) {
        allNodes.classed("selected", function (n) { return n.Id === node.Id });
        allLinks.classed("citation", function (l) { return l.type === 'cites' && l.target.Id === node.Id })
        allLinks.classed("reference", function (l) { return l.type === 'cites' && l.source.Id === node.Id })
        allLinks.classed("similar", function (l) { return l.type === 'similar' && (l.source.Id === node.Id || l.target.Id === node.Id) })
    }
    allLinks.attr('marker-end', (l) => {
        if (l.type !== 'similar'
            && (node?.Id === l.source.Id || node?.Id === l.target.Id)) {
            return 'url(#arrowhead)'
        }
        return 'none'
    })
}

function createTimeAxis(scale, width) {
    return d3.axisBottom(scale)
        .ticks(10)
        .tickFormat(function (d) {
            const fullYear = Math.floor(d)
            if (fullYear % 10 === 0) {
                return fullYear
            } else {
                return ''
            }
        })
}



