import React from "react";
import PropTypes from "prop-types";
import IconButton from "@mui/material/IconButton";
import Input from "@mui/material/Input";
import Paper from "@mui/material/Paper";
import SearchIcon from "@mui/icons-material/Search";
import TuneIcon from '@mui/icons-material/Tune';
import '../css/searchbar.css'

const styles = {
    root: {
        height: 42,
        display: "flex",
        justifyContent: "space-between",
    },
    iconButton: {
        color: '#aaa',
        transform: "scale(1, 1)",
        /*
        transition: theme.transitions.create(["transform", "color"], {
            duration: theme.transitions.duration.shorter,
            easing: theme.transitions.easing.easeInOut,
        }),*/
    },
    iconButtonHidden: {
        transform: "scale(0, 0)",
        "& > $icon": {
            opacity: 0,
        },
    },
    searchIconButton: {
        marginRight: 4,
    },
    input: {
        width: "100%",
    },
    searchContainer: {
        margin: "auto 16px",
        width: `calc(100% - ${0}px)`,
    },
};


const SearchBar = React.forwardRef(
    (
        {
            cancelOnEscape,
            className,
            classes,
            settingsIcon,
            showSettingsButton,
            highlightSettingsButton,
            highlightSearchButton,
            onClickSettings,
            disabled,
            onCancelSearch,
            onRequestSearch,
            searchIcon,
            style,
            ...inputProps
        },
        ref
    ) => {
        const inputRef = React.useRef();
        const [value, setValue] = React.useState(inputProps.value);

        React.useEffect(() => {
            setValue(inputProps.value);
        }, [inputProps.value]);

        const handleFocus = React.useCallback(
            (e) => {
                if (inputProps.onFocus) {
                    inputProps.onFocus(e);
                }
            },
            [inputProps.onFocus]
        );

        const handleBlur = React.useCallback(
            (e) => {
                setValue((v) => v.trim());
                if (inputProps.onBlur) {
                    inputProps.onBlur(e);
                }
            },
            [inputProps.onBlur]
        );

        const handleInput = React.useCallback(
            (e) => {
                setValue(e.target.value);
                if (inputProps.onChange) {
                    inputProps.onChange(e.target.value);
                }
            },
            [inputProps.onChange]
        );

        const handleCancel = React.useCallback(() => {
            setValue("");
            if (onCancelSearch) {
                onCancelSearch();
            }
        }, [onCancelSearch]);

        const handleRequestSearch = React.useCallback(() => {
            if (onRequestSearch) {
                onRequestSearch(value);
            }
        }, [onRequestSearch, value]);

        const handleClickSettings = React.useCallback(() => {
            if (onClickSettings) {
                onClickSettings();
            }
        }, [onClickSettings]);

        const handleKeyUp = React.useCallback(
            (e) => {
                if (e.charCode === 13 || e.key === "Enter") {
                    handleRequestSearch();
                } else if (
                    cancelOnEscape &&
                    (e.charCode === 27 || e.key === "Escape")
                ) {
                    handleCancel();
                }
                if (inputProps.onKeyUp) {
                    inputProps.onKeyUp(e);
                }
            },
            [handleRequestSearch, cancelOnEscape, handleCancel, inputProps.onKeyUp]
        );

        React.useImperativeHandle(ref, () => ({
            focus: () => {
                inputRef.current.focus();
            },
            blur: () => {
                inputRef.current.blur();
            },
        }));

        return (
            <Paper style={{ ...style, ...styles.root }}>
                <div style={styles.searchContainer}>
                    <Input
                        {...inputProps}
                        inputRef={inputRef}
                        onBlur={handleBlur}
                        value={value}
                        onChange={handleInput}
                        onKeyUp={handleKeyUp}
                        onFocus={handleFocus}
                        fullWidth
                        style={styles.input}
                        disableUnderline
                        disabled={disabled}
                    />
                </div>
                <IconButton
                    onClick={handleRequestSearch}
                    style={{ ...styles.iconButton, ...styles.searchIconButton }}
                    disabled={disabled}
                >
                    {React.cloneElement(searchIcon, {
                        style: styles.icon,
                        className: highlightSearchButton ? 'pulseElement' : '',
                        color: 'action'
                    })}
                </IconButton>
                {showSettingsButton && (
                    <IconButton
                        onClick={handleClickSettings}
                        style={styles.iconButton}
                        disabled={disabled}
                    >
                        {React.cloneElement(settingsIcon, {
                            color: highlightSettingsButton ? 'primary' : 'action'
                        })}
                    </IconButton>
                )}
            </Paper>
        );
    }
);

SearchBar.defaultProps = {
    className: "",
    showSettingsButton: true,
    highlightSettingsButton: false,
    highlightSearchButton: false,
    settingsIcon: <TuneIcon />,
    disabled: false,
    placeholder: "Search",
    searchIcon: <SearchIcon />,
    style: null,
    value: "",
};

SearchBar.propTypes = {
    /** Whether to clear search on escape */
    cancelOnEscape: PropTypes.bool,
    /** Override or extend the styles applied to the component. */
    classes: PropTypes.object,
    /** Custom top-level class */
    className: PropTypes.string,
    /** Override the close icon. */
    settingsIcon: PropTypes.node,
    /** Disables text field. */
    disabled: PropTypes.bool,
    /** Fired when the search is cancelled. */
    onCancelSearch: PropTypes.func,
    /** Fired when the text value changes. */
    onChange: PropTypes.func,
    /** Fired when the search icon is clicked. */
    onRequestSearch: PropTypes.func,
    /** Sets placeholder text for the embedded text field. */
    placeholder: PropTypes.string,
    /** Override the search icon. */
    searchIcon: PropTypes.node,
    /** Override the inline-styles of the root element. */
    style: PropTypes.object,
    /** The value of the text field. */
    value: PropTypes.string,
};

export default SearchBar;