import * as actions from './actions'

const initialState = {
    query: null,
    result: null,
    error: false,
    loading: false
};

export default function reducer(state = initialState, action) {
    switch (action.type) {
        case actions.SEARCH_QUERY:
            return {
                ...state,
                query: action.query,
                result: null,
                error: false,
                loading: true
            }
        case actions.SEARCH_ERROR:
            return {
                ...state,
                loading: false,
                error: true
            };
        case actions.SEARCH_RESULT:
            return {
                ...state,
                error: false,
                loading: false,
                result: action.result
            };
        default:
            return state;
    }
}