import * as actions from './actions'
import { call, put, takeLatest } from "redux-saga/effects";
import { search } from '../../services/api';


function* searchQuery(action) {
    try {
        const publications = yield call(search, action.query);
        const result = transformMockData(publications)
        yield put(actions.searchResult(result))
    } catch(error) {
        yield put(actions.searchError(error));
    }
}


function transformMockData(publications) {
    const publicationById = {}
    const citations = []
    const similarities = []
    let minYear = new Date().getFullYear();
    let maxYear = 0;
    let maxRelevanceScore = 0;
    let maxRelevancePublication;
    for(const publication of publications) {
        publicationById[publication.Id] = publication
        // add fake relevance score
        publication.relevanceScore = Math.random()
        if(publication.relevanceScore > maxRelevanceScore) {
            maxRelevanceScore = publication.relevanceScore
            maxRelevancePublication = publication
        }
        if(publication.year > maxYear) {
            maxYear = publication.year
        }
        if(publication.year && publication.year < minYear) {
            minYear = publication.year
        }
    }

    // add fake citations
    for(const publication of publications) {
        publication.citations = []
        const citationCount = Math.floor(Math.random() * 5) // up to 4 out citations
        for(var i = 0; i < citationCount; i++){
            const randomPublication = publications[Math.floor(Math.random() * publications.length)];
            publication.citations.push(randomPublication.Id)
            citations.push({
                type: 'cites',
                source: publication.Id,
                target: randomPublication.Id
            })
        }
    }

    // add fake similarities
    for(const publication of publications) {
        publication.similarPublications = []
        const similaritiesCount = Math.floor(Math.random() * 3) // up to 2 similar papers
        for(var i = 0; i < similaritiesCount; i++){
            const randomPublication = publications[Math.floor(Math.random() * publications.length)];
            publication.similarPublications.push(randomPublication.Id)
            similarities.push({
                type: 'similar',
                source: publication.Id,
                target: randomPublication.Id
            })
        }
    }

    return {
        publications,
        publicationById,
        citations,
        similarities,
        minYear,
        maxYear,
        maxRelevancePublication
    }
}

export function* watcherSaga() {
    yield takeLatest(actions.SEARCH_QUERY, searchQuery);
}