


const baseUrl = process.env.REACT_APP_BACKEND_URL

export async function search(query) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
            textQuery: query.textQuery,
            seedPaperIds: query.seedPaperIds,
            limit: query.maxPublicationCount
        })
    };
    const response = await fetch(`${baseUrl}/search`, requestOptions)
    const parsedResponse = await response.json()
    return parsedResponse
}

export async function queryPublications(textQuery) {
    const requestOptions = { method: 'GET' };
    const response = await fetch(`${baseUrl}/publications?q=${encodeURIComponent(textQuery)}`, requestOptions)
    const parsedResponse = await response.json()
    return parsedResponse
}

export async function fetchCitations(publicationId) {
    const requestOptions = { method: 'GET' };
    const response = await fetch(`${baseUrl}/publications/${publicationId}/citing`, requestOptions)
    const parsedResponse = await response.json()
    return parsedResponse
}


export async function fetchReferences(publicationId) {
    const requestOptions = { method: 'GET' };
    const response = await fetch(`${baseUrl}/publications/${publicationId}/referenced`, requestOptions)
    const parsedResponse = await response.json()
    return parsedResponse
}