import React, { useState, useEffect, useRef } from 'react'
import { connect } from 'react-redux';
import { compose } from "redux";
import Paper from '@mui/material/Paper';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import PublicationDetail from './PublicationDetail';
import '../css/hover.css';
import pinTabAnimation from '../assets/pin-tab.gif'
import Cookies from 'js-cookie'
import BrowseModal, { BrowseModalTypes } from './BrowseModal';


function Tab({
    title,
    isClosable,
    isSelected,
    isPinned,
    style,
    onClick = () => { },
    onDoubleClick = () => { },
    onClose = () => { }, }
) {
    const lastClick = useRef(null);

    return (
        <div
            onClick={() => {
                if (!isSelected) {
                    onClick()
                    return
                }
                const clickInFlight = lastClick.current
                lastClick.current = Date.now()
                if (clickInFlight) {
                    return
                }
                setTimeout(function () {
                    if ((Date.now() - lastClick.current) >= 300) {
                        onClick()
                    } else {
                        onDoubleClick()
                    }
                    lastClick.current = null
                }, 300);
            }}
            style={{
                display: 'inline-block',
                paddingRight: 8,
                paddingLeft: 12,
                color: isSelected ? 'black' : 'grey',
                cursor: 'pointer',
                fontSize: 16,
                fontStyle: isPinned ? 'normal' : 'italic',
                paddingTop: 6,
                paddingBottom: 8,
                borderStyle: isSelected ? 'solid' : 'solid',
                borderWidth: isSelected ? '0px 1px 2px 1px' : '0px 1px 2px 1px',
                boxSizing: 'border-box',
                borderColor: isSelected ? 'red #ddd #1565c0 #ddd' : '#fff',
                textAlign: 'center',
                userSelect: 'none',
                WebkitUserSelect: 'none',
                MozUserSelect: 'none',
                msUserSelect: 'none',
                ...style
            }}

            className={'hoverable-tab'}
        >
            <span style={{ marginRight: 8 }}>{title}</span>
            {isClosable && (
                <IconButton
                    className={'hoverable-tab-btn'}
                    color="primary"
                    aria-label="help"
                    sx={{ fontSize: 14, p: 0, m: 0 }}
                    onClick={(event) => {
                        event.stopPropagation()
                        onClose()
                    }}>
                    <CloseIcon sx={{ fontSize: 14 }} />
                </IconButton>
            )}
        </div>
    )
}

function tabTitle(publication) {
    const year = (publication.year === 0 ? '' : publication.year)
    let firstAuthorLastName;
    if (!publication.authors?.length) {
        firstAuthorLastName = ''
    } else {
        firstAuthorLastName = publication.authors[0].name.split(' ').pop()
    }

    if (year && firstAuthorLastName) {
        return `${firstAuthorLastName}, ${year}`
    } else if (firstAuthorLastName) {
        return firstAuthorLastName
    } else {
        return '(?)'
    }
}

function PublicationPanel({ search, selectedPublicationId, setSelectedPublicationId, addSeedPaper, addSeedPaperDisabled }) {

    const [publications, setPublications] = useState([]) // { Id: 'asdf', isPinned: false }
    const [showHelpTab, setShowHelpTab] = useState(!Boolean(Cookies.get('disableHelpTab')))
    const [helpTabSelected, setHelpTabSelected] = useState(false)
    const [browseModalState, setBrowseModalState] = useState({ open: false }) // { open: true, type: BrowseModalTypes.BROWSE_REFERENCES }

    useEffect(() => {
        if (helpTabSelected) {
            setHelpTabSelected(false)
        }
        if (selectedPublicationId && !publications.map((entry) => entry.Id).includes(selectedPublicationId)) {
            if (publications.length && !publications[publications.length - 1].isPinned) {
                setPublications([...publications.slice(0, -1), { Id: selectedPublicationId, isPinned: false }])
            } else {
                setPublications([...publications, { Id: selectedPublicationId, isPinned: false }])
            }
        }
    }, [selectedPublicationId])

    if (!selectedPublicationId) {
        return null;
    }

    function pinTab(publicationId) {
        const publicationsCopy = [...publications]
        publicationsCopy.forEach((entry) => {
            if (entry.Id === publicationId) {
                entry.isPinned = true
            }
        })
        setPublications(publicationsCopy)
    }

    function removeTab(publicationId) {
        const publicationsCopy = [...publications]
        const remaining = publicationsCopy.filter((entry) => (entry.Id !== publicationId))
        setPublications(remaining)
        if (publicationId === selectedPublicationId) {
            setSelectedPublicationId(remaining[0].Id)
        }
    }

    const tabList = publications.map((entry) => {
        const publication = search.result.publicationById[entry.Id]
        return (
            <Tab
                key={entry.Id}
                title={tabTitle(publication)}
                isClosable={publications.length > 1}
                isPinned={entry.isPinned}
                isSelected={entry.Id === selectedPublicationId && !helpTabSelected}
                onClick={() => {
                    setHelpTabSelected(false)
                    if (!(entry.Id === selectedPublicationId)) setSelectedPublicationId(entry.Id)
                }}
                onClose={() => removeTab(entry.Id)}
                onDoubleClick={() => pinTab(entry.Id)}
            />
        )
    })

    if (showHelpTab) {
        tabList.push(<Tab
            key={'?'}
            title={'?'}
            style={{
                color: '#42a5f5'
            }}
            isClosable={true}
            isPinned={true}
            isSelected={helpTabSelected}
            onClick={() => {
                if (!helpTabSelected) setHelpTabSelected(true)
            }}
            onClose={() => {
                Cookies.set('disableHelpTab', '1')
                setShowHelpTab(false)
                setHelpTabSelected(false)
            }}
        />)
    }

    return (
        <>
            <Paper
                elevation={5}
                sx={{ mr: 20, mt: 2, width: 'calc(100% - 14px)', display: 'flex', flexDirection: 'column', height: 'calc(100% - 30px)' }}
            >
                <div style={{
                    display: 'flex', flexWrap: 'wrap', marginBottom: 12,
                }}>
                    {tabList}
                </div>
                {(helpTabSelected && showHelpTab) ?
                    <div style={{ padding: 16, paddingTop: 24, fontFamily: 'Poppins' }}>
                        <div style={{ marginBottom: 12 }}>Double-click a publication's tab to keep it open:</div>
                        <img style={{ height: 96, display: 'block' }} src={pinTabAnimation} alt='tab pinning gif'></img>
                    </div>
                    :
                    <PublicationDetail
                        selectedPublication={selectedPublicationId}
                        onBrowseReferences={() => setBrowseModalState({ open: true, type: BrowseModalTypes.BROWSE_REFERENCED })}
                        onBrowseCitations={() => setBrowseModalState({ open: true, type: BrowseModalTypes.BROWSE_CITING })}
                        addSeedPaper={addSeedPaper}
                        addSeedPaperDisabled={addSeedPaperDisabled}
                    />
                }
            </Paper>
            <BrowseModal
                open={browseModalState.open}
                type={browseModalState.type}
                publicationId={selectedPublicationId}
                setSelectedPublicationId={setSelectedPublicationId}
                publicationShortName={tabTitle(search.result.publicationById[selectedPublicationId])}
                onClose={() => setBrowseModalState({ open: false })}
                addSeedPaper={addSeedPaper}
                addSeedPaperDisabled={addSeedPaperDisabled}
            />
        </>
    );
}

function mapStateToProps(state) {
    return { search: state.search }
}

function mapDispatchToProps(dispatch) {
    return {
        dispatch,
    };
}

const withConnect = connect(mapStateToProps, mapDispatchToProps);
export default compose(withConnect)(PublicationPanel);