
function getCiteKey(publication, fallback='your_citation_key') {
    const year = (publication.year === 0 ? '' : publication.year)
    let firstAuthorLastName;
    if (!publication.authors?.length) {
        firstAuthorLastName = ''
    } else {
        firstAuthorLastName = publication.authors[0].name.split(' ').pop()
    }

    if (year && firstAuthorLastName) {
        return `${firstAuthorLastName}${year}`
    } else if (firstAuthorLastName) {
        return firstAuthorLastName
    } else {
        return fallback
    }
}

export function generateBibTexFilename(publication) {
    return getCiteKey(publication, 'publication') + '.bib'
}

export function generateBibTexEntry(publication) {
    const citeKey = getCiteKey(publication)
    const author = publication.authors.map((author) => author.name).join(' and ')

    const doiEntry = publication.doi ? `,
    doi = {${publication.doi}}` : ''

    return `@article{${citeKey},
    author = {${author}},
    title = {${publication.title}},
    year = {${publication.year}},
    journal = {${publication.journalName}}${doiEntry}
}`
}