import React, { useState, useEffect, useRef } from 'react'
import { connect } from 'react-redux';
import { compose } from "redux";
import Modal from '@mui/material/Modal';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import CloseIcon from '@mui/icons-material/Close';
import AddIcon from '@mui/icons-material/Add';
import SearchBar from '../common/SearchBar';
import LoadingIndicator from '../common/LoadingIndicator';
import s2Icon from '../assets/s2.png'
import doiIcon from '../assets/doi.svg'
import pdfIcon from '../assets/pdf.png'
import { queryPublications } from '../../services/api'


function SeedPaperSearchModal({ open, onClose, addSeedPaper, addSeedPaperDisabled }) {

    const mounted = useRef(false);
    const [loading, setLoading] = useState(false)
    const [error, setError] = useState(null)
    const [publications, setPublications] = useState([])
    const [searchQuery, setSearchQuery] = useState('')
    const [expandedSet, setExpandedSet] = useState(new Set())
    const [showAllAuthorsSet, setShowAllAuthorsSet] = useState(new Set())

    useEffect(() => {
        mounted.current = true;
        return () => { mounted.current = false; };
    }, [])

    function onError(err) {
        console.log(err)
        if (mounted.current) {
            setError(err)
            setLoading(false)
        }
    }

    function onResult(response) {
        if (mounted.current) {
            setPublications(response)
            setLoading(false)
        }
    }

    const publicationList = publications.map((publication) => {
        const year = (publication.year === 0 ? '' : `(${publication.year})`)

        let authors
        if (!publication.authors?.length) {
            authors = ''
        } else if (publication.authors.length <= 2 || showAllAuthorsSet.has(publication.Id)) {
            authors = publication.authors.map((author) => author.name).reduce((prev, next) => (prev + ', ' + next))
        } else {
            const firstAuthors = publication.authors.slice(0, 2).map((author) => author.name).reduce((prev, next) => (prev + ', ' + next))
            authors =
                <div style={{ display: 'inline-block' }}>
                    {firstAuthors}
                    <Button
                        variant="text"
                        style={{ textTransform: 'none', fontSize: 16, marginTop: -4, marginLeft: -8 }}
                        onClick={() => {
                            const newSet = new Set(showAllAuthorsSet)
                            newSet.add(publication.Id)
                            setShowAllAuthorsSet(newSet)
                        }}
                        sx={{ p: 0, m: 0 }}
                    >
                        et al.
                    </Button>
                </div>
        }

        const pdfUrl = publication.pdfUrls.length > 0 ? publication.pdfUrls[0] : ''

        const maxAbstractChars = 300
        let trimmedPaperAbstract;
        let abstractWasTrimmed = false;
        if (publication.paperAbstract) {
            const trimmed = publication.paperAbstract.substr(0, maxAbstractChars);
            if (publication.paperAbstract.length > trimmed.length) {
                abstractWasTrimmed = true
            }
            // cut off at a word boundary
            trimmedPaperAbstract = trimmed.substr(0, Math.min(trimmed.length, trimmed.lastIndexOf(" "))) + ' '
        }

        return (
            <Paper elevation={3} sx={{ mb: 1 }} key={publication.Id}>
                <div style={{ padding: 16, position: 'relative' }}>
                    <IconButton
                        color="primary"
                        aria-label="close"
                        sx={{ p: 0, m: 0, float: 'right', clear: 'right' }}
                        disabled={addSeedPaperDisabled(publication)}
                        onClick={() => addSeedPaper(publication)} >
                        <AddIcon />
                    </IconButton>
                    <div style={{ marginBottom: 8, fontSize: 18 }}>{publication.title}</div>
                    <div style={{ marginBottom: 4, color: 'grey' }}>
                        <div style={{ display: 'inline-block', marginTop: 2, marginRight: 12 }}>
                            {pdfUrl && (
                                <img
                                    src={pdfIcon}
                                    alt="pdf icon"
                                    style={{ width: 18, marginRight: 8, cursor: 'pointer' }}
                                    onClick={() => window.open(pdfUrl, '_newtab')}
                                />
                            )}
                            {publication.doiUrl && (
                                <img
                                    src={doiIcon}
                                    alt="doi icon"
                                    style={{ width: 18, marginRight: 4, cursor: 'pointer' }}
                                    onClick={() => window.open(publication.doiUrl, '_newtab')}
                                />
                            )}
                            {publication.s2Url && (
                                <img
                                    src={s2Icon}
                                    alt="semantic scholar icon"
                                    style={{ width: 22, height: 18, cursor: 'pointer' }}
                                    onClick={() => window.open(publication.s2Url, '_newtab')}
                                />
                            )}
                        </div>
                        <div style={{ display: 'inline-block', verticalAlign: 'top' }}>
                            {authors} <span>{year}</span>
                        </div>
                    </div>
                    <div style={{ display: 'inline-block', marginBottom: 8, color: 'grey', fontSize: 14 }}>{`${publication.inCitations.length} citations`}</div>
                    {publication.paperAbstract && (
                        (expandedSet.has(publication.Id) || !abstractWasTrimmed) ? (<div style={{ textAlign: 'justify' }}>{publication.paperAbstract}</div>) : (
                            <div style={{ textAlign: 'justify' }}>{trimmedPaperAbstract}
                                <span><Button
                                    variant="text"
                                    style={{ textTransform: 'none', fontSize: 16, marginTop: -4 }}
                                    onClick={() => {
                                        const newSet = new Set(expandedSet)
                                        newSet.add(publication.Id)
                                        setExpandedSet(newSet)
                                    }}
                                    sx={{ p: 0, m: 0, color: '#888' }}
                                >
                                    Read more
                                </Button></span>
                            </div>
                        )
                    )}
                </div>
            </Paper >
        )
    })

    return (
        <Modal
            open={open}
            onClose={onClose}
        >
            <div
                onClick={(e) => e.stopPropagation()}
                style={{
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    transform: 'translate(-50%, -50%)',
                    width: '80%',
                    maxWidth: 800,
                    height: '80%',
                    backgroundColor: '#ffffff',
                    boxShadow: 24,
                    padding: 32,
                    paddingTop: 24,
                    paddingBottom: 0,
                    outline: 'none',
                    display: 'flex',
                    flexDirection: 'column'
                }}>
                <div>
                    <IconButton
                        color="primary"
                        aria-label="close"
                        sx={{ p: 0, m: 0, float: 'right', clear: 'right' }}
                        onClick={() => onClose()} >
                        <CloseIcon />
                    </IconButton>
                    <div style={{ marginBottom: 4, fontSize: 18 }}>{'Add Seed Publications'}</div>
                    <div style={{ display: 'flex' }}>
                        <SearchBar
                            value={searchQuery}
                            onChange={(newValue) => setSearchQuery(newValue)}
                            onRequestSearch={() => {
                                if (searchQuery) {
                                    setLoading(true)
                                    setError(null)
                                    setShowAllAuthorsSet(new Set())
                                    setExpandedSet(new Set())
                                    queryPublications(searchQuery)
                                        .then(onResult)
                                        .catch(onError)
                                }
                            }}
                            disabled={loading}
                            showSettingsButton={false}
                            placeholder={'Search by title, doi, authors'}
                            style={{
                                marginTop: 12,
                                marginBottom: 12,
                                width: '70%',
                                maxWidth: 540,
                            }}
                        />
                    </div>
                    <Divider />
                </div>
                {loading ? <LoadingIndicator size={100} style={{ marginTop: 200 }} /> : (
                    error ?
                        <div>
                            <div style={{ textAlign: "center", marginTop: 200, fontSize: 42 }}>⚠️</div>
                            <div style={{ textAlign: "center", color: 'grey' }}>
                                {'Something went wrong. Trying again may help.'}
                            </div>
                        </div> : (
                            publications.length === 0 ? (
                                <div>
                                    <div style={{ textAlign: "center", color: 'grey', marginTop: 200 }}>
                                        {'No publications found.'}
                                    </div>
                                </div>) :
                                (<div style={{
                                    overflow: 'scroll',
                                    paddingTop: 4,
                                    paddingLeft: 4,
                                    paddingRight: 4
                                }}
                                >
                                    {publicationList}
                                </div>))
                )}
            </div>
        </Modal>
    );
}

function mapStateToProps(state) {
    return { search: state.search }
}

function mapDispatchToProps(dispatch) {
    return {
        dispatch,
    };
}

const withConnect = connect(mapStateToProps, mapDispatchToProps);
export default compose(withConnect)(SeedPaperSearchModal);