import React, { useState } from 'react';
import SearchContainer from './search/SearchContainer';
import logo from './assets/logo.svg';
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import { MAX_SEED_PAPER_COUNT } from './constants/constants'

function Home() {
    const [searchQuery, setSearchQuery] = useState('')
    const [seedPapers, setSeedPapers] = useState({})
    const [maxPublicationCount, setMaxPublicationCount] = useState(100)
    const [searchConfigExpanded, setSearchConfigExpanded] = useState(false)


    const shouldHighlightSearchButton = () => {
        return Object.keys(seedPapers).length
    }

    const addSeedPaper = (paper) => {
        if (!seedPapers.hasOwnProperty(paper.Id)) {
            setSeedPapers({
                [paper.Id]: paper,
                ...seedPapers
            })
        }
    }

    const addSeedPaperDisabled = (p) => {
        return Object.keys(seedPapers).length >= MAX_SEED_PAPER_COUNT || seedPapers.hasOwnProperty(p.Id)
    }

    return (
        <Container>
            <div style={{
                position: 'absolute',
                maxWidth: 500,
                top: 4,
                right: 24,
                zIndex: 2
            }}>
                <Button variant="text" sx={{ width: 80, height: 36, fontFamily: 'Poppins', marginTop: 1 }}>About</Button>
                <Button variant="text" sx={{ width: 80, height: 36, fontFamily: 'Poppins', marginTop: 1 }}>Donate</Button>
            </div>
            <div>
                <div style={{ textAlign: 'center', marginBottom: 24 }}>
                    <img style={{
                        width: 200,
                        height: 200,
                        marginTop: 100,
                        marginBottom: 12
                    }} src={logo} alt="citrus logo" />
                    <div style={{ color: 'rgb(49%,40%,58%)', fontSize: 42, fontFamily: 'Poppins' }}>
                        citrus
                    </div>
                </div>
                <SearchContainer
                    style={{ position: 'static', margin: '0 auto' }}
                    searchQuery={searchQuery}
                    setSearchQuery={setSearchQuery}
                    shouldHighlightSearchButton={shouldHighlightSearchButton}
                    searchConfigExpanded={searchConfigExpanded}
                    setSearchConfigExpanded={setSearchConfigExpanded}
                    seedPapers={seedPapers}
                    setSeedPapers={setSeedPapers}
                    maxPublicationCount={maxPublicationCount}
                    setMaxPublicationCount={setMaxPublicationCount}
                    addSeedPaper={addSeedPaper}
                    addSeedPaperDisabled={addSeedPaperDisabled}
                />
            </div>
        </Container>
    );
}

export default Home;