import tinyColor from 'tinycolor2'
import seedrandom from 'seedrandom'


export default function generateColorTuple(id) {
    const rng = seedrandom.xor4096(id)
    const color1 = tinyColor.mix(tinyColor.fromRatio({r: rng(), g: rng(), b: rng()}).saturate(10), tinyColor('#fff'), 30).toHexString()
    const color2 = tinyColor.mix(tinyColor.fromRatio({r: rng(), g: rng(), b: rng()}).saturate(10), tinyColor('#fff'), 30).toHexString()
    return [color1, color2]
}