import React, { useState } from 'react'
import { connect } from 'react-redux';
import { compose } from "redux";
import Paper from "@mui/material/Paper";
import IconButton from "@mui/material/IconButton";
import Slider from '@mui/material/Slider';
import SearchBar from '../common/SearchBar';
import AddIcon from '@mui/icons-material/Add';
import CloseIcon from '@mui/icons-material/Close';
import Divider from '@mui/material/Divider';
import { useHistory } from 'react-router-dom'
import SeedPaperSearchModal from './SeedPaperSearchModal'
import { MAX_SEED_PAPER_COUNT } from '../constants/constants'
import generateColorTuple from '../constants/colors'
import Citrus from '../common/Citrus'


function shortName(publication) {
    const year = (publication.year === 0 ? '' : publication.year)
    let firstAuthorLastName;
    if (!publication.authors?.length) {
        firstAuthorLastName = ''
    } else {
        firstAuthorLastName = publication.authors[0].name.split(' ').pop()
    }

    if (year && firstAuthorLastName) {
        return `${firstAuthorLastName}, ${year}`
    } else if (firstAuthorLastName) {
        return firstAuthorLastName
    } else {
        return ''
    }
}

function SearchContainer({
    style,
    search,
    searchQuery,
    setSearchQuery,
    shouldHighlightSearchButton,
    searchConfigExpanded,
    setSearchConfigExpanded,
    seedPapers,
    setSeedPapers,
    maxPublicationCount,
    setMaxPublicationCount,
    addSeedPaper,
    addSeedPaperDisabled
}) {
    const history = useHistory()
    const [seedPaperSearchOpen, setSeedPaperSearchOpen] = useState(false)

    const seedPaperIds = Object.keys(seedPapers)

    return (
        <div onClick={() => searchConfigExpanded && !seedPaperSearchOpen && setSearchConfigExpanded(false)} style={{
            position: 'absolute',
            width: '60%',
            top: 4,
            left: '0',
            zIndex: 2,
            ...style
        }}>
            <SeedPaperSearchModal
                open={seedPaperSearchOpen}
                onClose={() => {
                    setSeedPaperSearchOpen(false)
                    setTimeout(() => setSearchConfigExpanded(true))
                }}
                addSeedPaper={addSeedPaper}
                addSeedPaperDisabled={addSeedPaperDisabled}
            />
            <SearchBar
                disabled={search.loading}
                value={searchQuery}
                highlightSettingsButton={searchConfigExpanded}
                highlightSearchButton={shouldHighlightSearchButton()}
                onClickSettings={() => setSearchConfigExpanded(!searchConfigExpanded)}
                onChange={(newValue) => setSearchQuery(newValue)}
                onRequestSearch={() => {
                    if (searchQuery || seedPaperIds.length) {
                        history.push(`/search?q=${encodeURIComponent(searchQuery)}&s=${seedPaperIds.join(',')}&l=${maxPublicationCount}`)
                    }
                }}
                placeholder={'Search for a research area, publication or author'}
                style={{
                    maxWidth: 500,
                    margin: '0 auto'
                }}
            />
            {searchConfigExpanded && (
                <Paper
                    sx={{
                        margin: '8px auto'
                    }}
                    style={{ padding: 12, paddingTop: 20, maxWidth: 600 }}
                    elevation={3}
                    onClick={(e) => {
                        e.stopPropagation()
                    }}
                >
                    <div>
                        <div style={{ fontFamily: 'Poppins' }}>Max number of publications</div>
                        <Divider style={{ marginTop: 4, marginBottom: 12 }} />
                        <Slider
                            aria-label="Max number of publications"
                            value={maxPublicationCount}
                            onChange={(event) => setMaxPublicationCount(event.target.value)}
                            getAriaValueText={() => maxPublicationCount}
                            valueLabelDisplay="auto"
                            step={10}
                            min={10}
                            max={500}
                        />
                        <div style={{ fontFamily: 'Poppins', marginTop: 12 }}>
                            Seed publications
                                {seedPaperIds.length !== 0 &&
                                <IconButton
                                    color="primary"
                                    aria-label="add seed paper"
                                    sx={{ p: 0, float: 'right' }}
                                    style={{ marginRight: 14 }}
                                    onClick={() => setSeedPaperSearchOpen(true)}
                                    disabled={seedPaperIds.length >= MAX_SEED_PAPER_COUNT}
                                >
                                    <AddIcon fontSize="medium" />
                                </IconButton>
                            }
                        </div>
                        <Divider style={{ marginTop: 4, marginBottom: 12 }} />
                        {seedPaperIds.length ? (
                            <div style={{ maxHeight: 400, overflow: 'scroll' }}>{
                                Object.entries(seedPapers).map(([id, paper]) => {
                                    const [color1, color2] = generateColorTuple(paper.Id)
                                    return (
                                        <Paper key={paper.Id} sx={{ m: 1, p: 1, fontSize: 14 }} elevation={3}>
                                            <IconButton
                                                color="primary"
                                                aria-label="remove seed paper"
                                                sx={{ p: 0, m: 0, float: 'right', clear: 'right' }}
                                                onClick={() => {
                                                    delete seedPapers[paper.Id]
                                                    setSeedPapers({ ...seedPapers })
                                                }} >
                                                <CloseIcon fontSize="small" />
                                            </IconButton>
                                            <div style={{ marginBottom: 8 }}>{paper.title}</div>
                                            <div style={{ display: 'inline-block', marginRight: 12 }}>
                                                <Citrus color1={color1} color2={color2} width={28} />
                                            </div>
                                            <span style={{
                                                display: 'inline-block',
                                                verticalAlign: 'top',
                                                marginTop: 4,
                                                color: 'grey'
                                            }}>{
                                                    shortName(paper)}
                                            </span>
                                        </Paper>
                                    )
                                })
                            }</div>
                        ) : (
                                <div>
                                    <div style={{ fontSize: 15, textAlign: 'center', marginTop: 16 }}>
                                        Seeds point Citrus toward your specific research domain.
                                        </div>
                                    <IconButton
                                        style={{ margin: '0 auto', display: 'block' }}
                                        color='primary'
                                        onClick={() => setSeedPaperSearchOpen(true)}
                                    >
                                        <AddIcon color='primary' fontSize='large' />
                                    </IconButton>
                                </div>
                            )}
                    </div>
                </Paper>
            )}
        </div>
    )
}

function mapStateToProps(state) {
    return { search: state.search }
}

function mapDispatchToProps(dispatch) {
    return {
        dispatch,
    };
}

const withConnect = connect(mapStateToProps, mapDispatchToProps);
export default compose(withConnect)(SearchContainer);