import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux';
import { compose } from "redux";
import IconButton from "@mui/material/IconButton";
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import Switch from '@mui/material/Switch';
import ForceGraph from './ForceGraph';
import '../css/legend.css'
import Legend from './Legend';
import PublicationPanel from './PublicationPanel'


function SearchResults({ search, addSeedPaper, addSeedPaperDisabled }) {

    const [timeLineEnabled, setTimeLineEnabled] = useState(true)
    const [selectedPublicationId, setSelectedPublicationId] = useState(search.result.maxRelevancePublication.Id)
    const [detailExpanded, setDetailExpanded] = useState(true)
    const [graphForceUpdateProp, graphForceUpdate] = useState(Math.random())

    useEffect(() => {
        // For some weird reason this is necessary.
        // It seems like the size change resulting from the new gridTemplateColumns
        // takes effect too late in the dom for ForeceGraph's shouldComponentUpdate to catch it.
        setTimeout(() => graphForceUpdate(Math.random()))
    }, [detailExpanded])

    return (
        <React.Fragment>
            <ForceGraph
                fakePropForForceUpdate={graphForceUpdateProp}
                graph={search.result}
                graphWidth={detailExpanded ? 0.6 : 1.0}
                clipNodesToTimeline={timeLineEnabled}
                onPublicationSelected={(Id) => setSelectedPublicationId(Id)}
                selectedPublication={selectedPublicationId}
                seedPaperIds={search.query.seedPaperIds}
            />
            <div
                style={{
                    position: 'absolute',
                    bottom: 36,
                    right: (detailExpanded ? 'calc(40% + 24px)' : 24),
                    transition: 'right 0.5s ease-out'
                }}
            >
                <Switch
                    checked={timeLineEnabled}
                    onChange={(event) => {
                        setTimeLineEnabled(!timeLineEnabled)
                    }}
                />
            </div>
            <Legend />
            <IconButton
                onClick={() => setDetailExpanded(!detailExpanded)}
                color='primary'
                sx={{
                    height: 36,
                    width: 36,
                    position: 'absolute',
                    top: 60,
                    right: detailExpanded ? '40%' : 12,
                    transition: 'right 0.5s ease-out',
                    zIndex: 4
                }}
            >
                {detailExpanded ? (<ArrowForwardIosIcon fontSize="small" />) : (<ArrowBackIosNewIcon fontSize="small" />)}
            </IconButton>
            <div style={{
                position: 'absolute',
                top: 48,
                width: '40%',
                left: detailExpanded ? '60%' : '100%',
                bottom: 12,
                transition: 'left 0.5s ease-out'
            }}>
                <PublicationPanel
                    selectedPublicationId={selectedPublicationId}
                    setSelectedPublicationId={setSelectedPublicationId}
                    addSeedPaper={addSeedPaper}
                    addSeedPaperDisabled={addSeedPaperDisabled}
                />
            </div>
        </React.Fragment>
    );
}

function mapStateToProps(state) {
    return { search: state.search }
}

function mapDispatchToProps(dispatch) {
    return {
        dispatch,
    };
}

const withConnect = connect(mapStateToProps, mapDispatchToProps);
export default compose(withConnect)(SearchResults);